import { ItemServiceProvider } from './../../providers/item-service/item-service';
import { Fornecedor } from './../../entity/Fornecedor';
import { Produto } from './../../entity/Produto';
//import { Observable } from 'rxjs/Observable';
//import { Base64 } from '@ionic-native/base64';
import { ProdutoServiceProvider } from './../../providers/produto-service/produto-service';
import { TabsPage } from './../tabs/tabs';
import { Utils } from './../../entity/Utils';
import { LoadingController, ToastController } from 'ionic-angular';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
//import { FileTransfer, FileTransferObject, FileUploadOptions } from '@ionic-native/file-transfer';
import { HttpEventType, HttpResponse } from '@angular/common/http';
import { FornecedorServiceProvider } from '../../providers/fornecedor-service/fornecedor-service';

@IonicPage()
@Component({
  selector: 'page-produto-edit',
  templateUrl: 'produto-edit.html',
  providers: [ProdutoServiceProvider, FornecedorServiceProvider, ItemServiceProvider]
})
export class ProdutoEditPage {
  showItens: boolean;
  showFile: boolean;
  showImage1: boolean;
  showImage2: boolean;
  showImage3: boolean;
  produtoUrl:string;
  model: Produto;
  fornecedores: any;
  fornecedor_id: number;
  itens: any;
  item_id: number;
  imgPreview = 'assets/imgs/blank-avatar.jpg';

  selectedFiles1: FileList;
  currentFileUpload1: File;
  progress1: { percentage: number } = { percentage: 0 };

  selectedFiles2: FileList;
  currentFileUpload2: File;
  progress2: { percentage: number } = { percentage: 0 };

  selectedFiles3: FileList;
  currentFileUpload3: File;
  progress3: { percentage: number } = { percentage: 0 };

  constructor(public navCtrl: NavController,
              public loadingCtrl: LoadingController,
              public toastCtrl: ToastController,
              public navParams: NavParams,
              private produtoProvider: ProdutoServiceProvider,
              private fornecedorProvider: FornecedorServiceProvider,
              private itemProvider: ItemServiceProvider) 
  {
    this.produtoUrl = Utils.getUrlBackEnd() + "produto/";
      
    if (this.navParams.data.produto) {
      this.model = this.navParams.data.produto;
      if (this.model.fornecedor == null) {
        this.model.fornecedor = new Fornecedor();
      } else {
        this.fornecedor_id = this.model.fornecedor.id;
      }
  
      if (this.model.imagem1 != null) 
        this.showImage1 = true;
      else
        this.showImage1 = false;

      if (this.model.imagem2 != null) 
        this.showImage2 = true;
      else
        this.showImage2 = false;

      if (this.model.imagem3 != null) 
        this.showImage3 = true;
      else
        this.showImage3 = false;

    } else {
      this.model = new Produto();
    }
  }
  
  save() {
    this.saveProduto().subscribe(
      (res: Produto) => {
        this.model = res;
      },
        (error) => {
          this.toastCtrl.create({ message: 'Erro ao salvar o produto. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
      },
      () => {
        if (this.fornecedor_id)
          this.updateFornecedor(this.model.id, this.fornecedor_id);
        this.uploadImages(this.model.id);

        this.toastCtrl.create({ message: 'Produto salvo com sucesso. Id: ' + this.model.id, position: 'botton', duration: 3000 }).present();
        this.navCtrl.setRoot(TabsPage)
      }
    );
  }

  updateFornecedor(id: number, fornecedor_id: number) {
    this.produtoProvider.updateFornecedor(id, fornecedor_id).subscribe(res => console.log(res));
    console.log("updateFornecedor");
  }

  addItem() {
    this.saveProduto().subscribe(
      (res: Produto) => {
        this.model = res;
      },
        (error) => {
          this.toastCtrl.create({ message: 'Erro ao salvar o produto. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
      },
      () => {
        if (this.item_id)
          this.produtoProvider.addItem(this.model.id, this.item_id).subscribe(res => console.log(res));
        this.toastCtrl.create({ message: 'Item adicionado ao produto.', position: 'botton', duration: 3000 }).present();
      }
    );
  }


  selectFile1(event) {
    this.selectedFiles1 = event.target.files;
  }
  
  selectFile2(event) {
    this.selectedFiles2 = event.target.files;
  }

  selectFile3(event) {
    this.selectedFiles3 = event.target.files;
  }

  upload1() {
    if (!this.model.id) {
      this.saveProduto().subscribe(
        (res: Produto) => {
          this.model = res;
        },
          (error) => {
            this.toastCtrl.create({ message: 'Erro ao salvar o produto. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
        },
        () => {
          this.uploadImage1(this.model.id);
        }
      );    
    } else {
      this.uploadImage1(this.model.id);
    }
  }

  upload2() {
    if (!this.model.id) {
      this.saveProduto().subscribe(
        (res: Produto) => {
          this.model = res;
        },
          (error) => {
            this.toastCtrl.create({ message: 'Erro ao salvar o produto. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
        },
        () => {
          this.uploadImage2(this.model.id);
        }
      );    
    } else {
      this.uploadImage2(this.model.id);
    }
  }

  showFiles(enable: boolean) {
    this.showFile = enable
  }

  changeShowItens(enable: boolean) {
    this.showItens = enable
  }
  
  upload3() {
    if (!this.model.id) {
      this.saveProduto().subscribe(
        (res: Produto) => {
          this.model = res;
        },
          (error) => {
            this.toastCtrl.create({ message: 'Erro ao salvar o produto. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
        },
        () => {
          this.uploadImage3(this.model.id);
        }
      );    
    } else {
      this.uploadImage3(this.model.id);
    }
  }

  uploadImages(id: number) {
    this.progress1.percentage = 0;
 
    if (this.selectedFiles1 != undefined)
      this.currentFileUpload1 = this.selectedFiles1.item(0);
    if (this.selectedFiles2 != undefined)
      this.currentFileUpload2 = this.selectedFiles2.item(0);
    if (this.selectedFiles3 != undefined)
      this.currentFileUpload3 = this.selectedFiles3.item(0);
    console.log(this.currentFileUpload1);
    console.log(this.currentFileUpload2);
    console.log(this.currentFileUpload3);
    this.produtoProvider.pushFilesToStorage(id, this.currentFileUpload1, this.currentFileUpload2, this.currentFileUpload3).subscribe(event => {
      if (event.type === HttpEventType.UploadProgress) {
        this.progress1.percentage = Math.round(100 * event.loaded / event.total);
      } else if (event instanceof HttpResponse) {
        console.log('Files are completely uploaded!');
      }
    });

    this.selectedFiles1 = undefined;
  }

  uploadImage1(id: number) {
    this.progress1.percentage = 0;
 
    this.currentFileUpload1 = this.selectedFiles1.item(0);
    this.produtoProvider.pushFileToStorage1(id, this.currentFileUpload1).subscribe(event => {
      if (event.type === HttpEventType.UploadProgress) {
        this.progress1.percentage = Math.round(100 * event.loaded / event.total);
      } else if (event instanceof HttpResponse) {
        console.log('File is completely uploaded!');
      }
    });

    this.selectedFiles1 = undefined;
  }

  uploadImage2(id: number) {
    this.progress2.percentage = 0;
 
    this.currentFileUpload2 = this.selectedFiles2.item(0);
    this.produtoProvider.pushFileToStorage2(id, this.currentFileUpload2).subscribe(event => {
      if (event.type === HttpEventType.UploadProgress) {
        this.progress2.percentage = Math.round(100 * event.loaded / event.total);
      } else if (event instanceof HttpResponse) {
        console.log('File is completely uploaded!');
      }
    });

    this.selectedFiles2 = undefined;
  }

  uploadImage3(id: number) {
    this.progress3.percentage = 0;
 
    this.currentFileUpload3 = this.selectedFiles3.item(0);
    this.produtoProvider.pushFileToStorage3(id, this.currentFileUpload3).subscribe(event => {
      if (event.type === HttpEventType.UploadProgress) {
        this.progress3.percentage = Math.round(100 * event.loaded / event.total);
      } else if (event instanceof HttpResponse) {
        console.log('File is completely uploaded!');
      }
    });

    this.selectedFiles3 = undefined;
  }

  private saveProduto() {
    if (this.model.id) {
      return this.produtoProvider.update(this.model);
    } else {
      return this.produtoProvider.insert(this.model);
    }
  }
  
  
  presentToast(msg) {
    let toast = this.toastCtrl.create({
      message: msg,
      duration: 3000,
      position: 'bottom'
    });
  
    toast.onDidDismiss(() => {
      console.log('Dismissed toast');
    });
  
    toast.present();
  }

  getAllFornecedores() {    
    this.fornecedorProvider.listAll().subscribe(
      response => this.fornecedores = response
    );
  }

  getAllItens() {    
    this.itemProvider.listAll().subscribe(
      response => this.itens = response
    );
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProdutoEditPage');
    this.getAllFornecedores();
    this.getAllItens();
  }

}



